"use client";

import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { useEffect, useState } from "react";
import { Exam } from "@/types";
import { CreateExamDialog } from "@/components/create-exam-dialog";
import { useRouter } from "next/navigation";
import axios from "axios";

export default function Home() {
  const [exams, setExams] = useState<Exam[]>([]);
  const router = useRouter();

  const API_BASE = "http://localhost:3000"; // ← локальный бэкенд

  useEffect(() => {
    fetchExams();
  }, []);

  const fetchExams = async () => {
    try {
      const response = await axios.get(`${API_BASE}/exams`);
      setExams(response.data);
    } catch (error) {
      console.error("Ошибка при загрузке экзаменов:", error);
    }
  };

  const handleExamClick = (examId: string) => {
    router.push(`/exam/${examId}`);
  };

  const handleCreateExam = async (newExam: Partial<Exam>) => {
    try {
      const response = await axios.post(`${API_BASE}/exams`, newExam);
      setExams((prev) => [...prev, response.data]);
    } catch (error) {
      console.error("Ошибка при создании экзамена:", error);
    }
  };

  const handleDeleteExam = async (examId: string) => {
    try {
      await axios.delete(`${API_BASE}/exams/${examId}`);
      setExams((prev) => prev.filter((exam) => exam.id !== examId));
    } catch (error) {
      console.error("Ошибка при удалении экзамена:", error);
    }
  };

  const handleEditExam = async (updatedExam: Exam) => {
    try {
      const response = await axios.put(
        `${API_BASE}/exams/${updatedExam.id}`,
        updatedExam
      );
      setExams((prev) =>
        prev.map((exam) => (exam.id === updatedExam.id ? response.data : exam))
      );
    } catch (error) {
      console.error("Ошибка при редактировании экзамена:", error);
    }
  };

  return (
    <div className="flex flex-col items-center justify-start min-h-screen py-10">
      <h1 className="text-4xl font-bold mb-4 text-primary">Exam AI Grader</h1>
      <section className="w-full max-w-4xl">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-2xl font-semibold text-foreground">Exams</h2>
          <CreateExamDialog onCreateExam={handleCreateExam} />
        </div>
        <div className="grid gap-4 grid-cols-1 md:grid-cols-2 lg:grid-cols-3">
          {exams.map((exam) => (
            <Card
              key={exam.id}
              className="relative hover:shadow-md transition-shadow"
            >
              <CardHeader
                onClick={() => handleExamClick(exam.id)}
                className="cursor-pointer"
              >
                <CardTitle>{exam.name}</CardTitle>
                <CardDescription>{exam.description}</CardDescription>
              </CardHeader>
              <CardContent>
                <p>Tasks: {exam.tasks?.length ?? 0}</p>
                <div className="flex gap-2 mt-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() =>
                      handleEditExam({
                        ...exam,
                        name: prompt("Новое название", exam.name) || exam.name,
                      })
                    }
                  >
                    Редактировать
                  </Button>
                  <Button
                    variant="destructive"
                    size="sm"
                    onClick={() => handleDeleteExam(exam.id)}
                  >
                    Удалить
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </section>
    </div>
  );
}
